@media only screen and (max-width: 960px) {
  .gallery {
    text-align: center;
  }
}

.gallery__pic-frame {
  display: inline-block;
  background-color: #f3af9d;
  border: 1px solid #e8603c;
}

.gallery--small .gallery__pic-frame {
  margin: 2px;
  height: 48px;
  width: 48px;
}

.gallery--small img {
  width: 46px;
  height: 46px;
}

footer .container-fluid {
  background-color: #f3af9d;
}

footer .container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 960px;
  background-color: #fdfdfd;
}

.info {
  display: flex;
  flex-direction: row;
  padding: 35px;
  background-color: #e8603c;
}

.info h4 {
  padding-bottom: 15px;
  color: #fff;
  font-family: "BebasBold", sans-serif;
  font-size: 24px;
  font-weight: 700;
  font-style: bold;
}

@media only screen and (max-width: 480px) {
  .info {
    flex-direction: column;
  }
}

.info__about {
  max-width: 281px;
  font-family: "HelveticaNeueRoman", sans-serif;
  font-size: 13px;
  font-weight: 400;
  font-style: normal;
  color: #fff;
  text-align: left;
}

.info__about p {
  margin-bottom: 12px;
  line-height: 1.25em;
}

.info__twitter {
  padding-left: 60px;
}

@media only screen and (max-width: 480px) {
  .info__twitter {
    margin-top: 30px;
    padding-left: 0;
  }
}

.tweet {
  padding-bottom: 15px;
  max-width: 212px;
  font-family: "HelveticaNeueRoman", sans-serif;
  font-size: 13px;
  font-weight: 400;
  font-style: normal;
  color: #fff;
  text-align: left;
  line-height: 1.25em;
}

.tweet__auth, .tweet__date {
  color: #f3af9d;
  cursor: pointer;
  text-decoration: none;
}

.tweet__auth:hover, .tweet__date:hover {
  text-decoration: underline;
}

.tweet__date {
  margin-top: 5px;
}
